.chatSection { 
    -webkit-box-shadow: 1px 2px 5px 1px #737272; 
            box-shadow: 1px 2px 5px 1px #737272;
    border-radius: 5px;
    margin: 0 auto;
    border: solid #000 2px;
    background-image: url('pixelBackground.jpg');
    background-repeat: no-repeat;
    background-size: 100%;
    overflow: hidden;
}
.chatSection > .col-12:nth-child(1) {
    border-bottom: solid #000 2px;
    height: 200px;
    border-radius: 5px 5px 0 0;
    padding: 5px;
    overflow: hidden; 
    font-size: 12px; 
    
}
.chatBox {position: absolute; bottom: 0}
.chatSection > div > button {position: absolute; right: 5px; z-index: 5}
.chatPlayer {float: left; padding-right: 5px; font-weight: bold; color: rgb(0, 116, 161)}
.chatInput > .col-10 {padding: 0; margin: 0}
.chatInput > .col-10 > input {
    padding-left: 5px;
    height: 30px;
    width: 100%; 
    margin: 0;
    color: #000;
    font-size: 12px;
    border-radius: 0;
    border: none
}
.chatInput > .col-2 {padding: 0} 
.col-2 > button {
    width: 100%; 
    border: none; 
    border-left: solid #000 2px; 
    border-radius: 0; 
    padding: 0; 
    margin: 0;
    height: 100%; 
}
.col-2 > button:disabled {background: rgb(112, 2, 2)}
.Hoebear {color: rgb(194, 44, 186)}


