.inputContainer {
    border: solid #000 2px;
    border-radius: 5px;
    background: -webkit-gradient(linear, right top, left top, from(rgba(0, 0, 0, 0.166)), to(rgba(0, 0, 0)));
    background: -webkit-linear-gradient(right, rgba(0, 0, 0, 0.166), rgba(0, 0, 0));
    background: -o-linear-gradient(right, rgba(0, 0, 0, 0.166), rgba(0, 0, 0));
    background: linear-gradient(to left, rgba(0, 0, 0, 0.166), rgba(0, 0, 0));
    background-image: url('pixelBackground.jpg');
    background-repeat: no-repeat;
    background-size: 100%;
    -webkit-box-shadow: 1px 2px 5px 1px #737272;
            box-shadow: 1px 2px 5px 1px #737272;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 15px 0 -15px;
    height: 0;
    opacity: 0;
    overflow: hidden;
    -webkit-transition: height 4000ms, opacity 4000ms;
    -o-transition: height 4000ms, opacity 4000ms;
    transition: height 4000ms, opacity 4000ms; 
}
.levelsInput{
   margin: 15px -15px;
   text-align: center;
   font-size: 15px;
}
.levelsInput > div {height: 95px; overflow: hidden}  

.levelsInput > div > input {
    position: relative;
    height: 20px;
    width: 100%; 
    z-index: 3;
    text-align: center;
    background: none;
    border: none;
}
.levelsInput > div > #totemBackground {
    position: absolute;
    
    /* opacity: .3; */
    left: 0;
    z-index: 1;
    
}

.levelsInput > div > #totemHead {
    z-index: 2;
    position: absolute;
    height: 150px;
    /* border: solid red 1px; */
    top: -23px;
    right: -240px;
    margin: 0 auto;
   
    
}

.levelsInput > div > h4 {
   
    position: relative;
    font-size: 20px;
    z-index: 3;
    margin-bottom: -2px ;
    font-family: kalimba;
    letter-spacing: 1px;
    
    text-shadow: 1px 1px 6px #fff;
}

.inputContainer > div > h2 { 
    text-align: center;
    font-family: kalimba;
    letter-spacing: 1px;
    text-shadow: 1px 1px 6px #fff;
    padding: 0;
    padding: 5px;
    margin: 0;
}

.inputContainer > div > .row > .col-10 > h4 {
   
    text-align: center;
    font-family: kalimba;
    margin: 3px 0;
    padding: 0;
    text-shadow: 1px 1px 6px #fff;
    
}

.inputContainer > div > .row > .col-10 {padding-top: 2px; background: #fff}

.inputContainer > div > .row > .col-2 > button {
    position: absolute;
    right: 0;
    border-left: solid #000 2px;
    height: 100%;
    border-radius: 0;
    background: -webkit-gradient(linear, left top, right top, from(rgba(171, 86, 255, 0.317)), to(rgba(128, 2, 255, 0.317)));
    background: -webkit-linear-gradient(left, rgba(171, 86, 255, 0.317), rgba(128, 2, 255, 0.317));
    background: -o-linear-gradient(left, rgba(171, 86, 255, 0.317), rgba(128, 2, 255, 0.317));
    background: linear-gradient(to right, rgba(171, 86, 255, 0.317), rgba(128, 2, 255, 0.317));
}

.inputContainer > div > .row > .col-2 > button:hover {background:rgba(118, 59, 177, 0.6)}
.inputContainer > div:nth-child(1) {border-bottom: solid #000 2px; background: -webkit-gradient(linear, left top, right top, from(rgba(171, 86, 255, 0.317)), to(rgba(171, 86, 255, 0.417))); background: -webkit-linear-gradient(left, rgba(171, 86, 255, 0.317), rgba(171, 86, 255, 0.417)); background: -o-linear-gradient(left, rgba(171, 86, 255, 0.317), rgba(171, 86, 255, 0.417)); background: linear-gradient(to right, rgba(171, 86, 255, 0.317), rgba(171, 86, 255, 0.417))}
.inputContainer > div:nth-child(3) {border-top: solid #000 2px}


